//岗位
import { Component, OnInit } from '@angular/core';
import { PostsManageBean } from './bean/posts-manage.bean'
import { CommonHttpService } from '../../http/common.http.service';
import { SharedBasisModule } from '../../../../../../app/sharedBasis.module';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
// import { ManagePermission, ManageLevel } from '../../constant/common.codeValue'
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'posts-manage',
    templateUrl: './posts-manage.html',
    styleUrls: ['posts-manage.scss'],
    providers: [CommonHttpService, ConfirmationService, Commfunc]
})

//岗位管理
export class PostsManage {
    manageLevels: any;
    //管理权限
    managePermission: any = [];
    //管理级别
    // manageLevel: any = ManageLevel;
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    precredit: string = '岗位查询';

    // 表体内容
    tableData: any;
    postsManageBean: PostsManageBean = new PostsManageBean()

    //总页数
    total: string;
    //每页多少条
    pageSize: number = 10;

    //模态框的标题
    headerTitle: string = "机构详情";
    //模态框是否显示
    display: boolean = false;
    showModel: string;
    //传出的值
    inValue: string;

    //修改的参数
    updateValue: any = [];

    //分配角色传参
    updateValueRole: any = [];
    //提示信息
    msgs: Message[] = [];
    //当前页
    currentPage: number;
    rows: any;//当前页显示条数
    first: number = 0;//分页控制
    code: string;
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {
        this.codeValues()
        this.managePermission = this.code["ManagePermission"];
        this.manageLevels = this.code['manageLevels'];
    }

    ngOnInit() {
        this.postsManageBean.pageSize = 10;
        this.postsManageBean.pageNum = 1;
        this.queryClick();
        //按回车键查询
        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.query();
            }
        }
    }
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询
    queryClick() {
        this.commonHttpService.queryPostById(this.postsManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.postInfoList;
                this.total = data.total;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    query() {
        this.first = 0;
        this.postsManageBean.pageNum = 1;
        this.commonHttpService.queryPostById(this.postsManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.postInfoList;
                this.total = data.total;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //分配角色按钮事件
    allotRole(item) {
        this.headerTitle = '角色分配';
        this.display = true;
        this.showModel = '4';
        this.updateValue = { postId: item.postId, postName: item.postName };
    }
    allotRoleCall(param) {
        this.display = param;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: 'success' });
    }
    // 增加按钮事件
    add() {
        this.headerTitle = '岗位添加';
        this.display = true;
        this.showModel = '1';
    }
    //添加的回调
    addCall(param) {
        this.display = param;
        // this.postsManageBean.pageNum = 1;
        this.queryClick();
    }

    //修改按钮事件
    update(item) {
        this.headerTitle = '岗位修改';
        this.display = true;
        this.showModel = '2';
        this.updateValue = item;
    }
    //修改的回调
    updateCall(param) {
        this.display = param;
        // this.postsManageBean.pageNum = this.currentPage;
        // this.postsManageBean = new PostsManageBean();
        this.queryClick();
    }

    //岗位导入
    into() {
        this.headerTitle = '柜员岗位导入';
        this.display = true;
        this.showModel = '3';
    }

    //删除按钮事件
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                // 条件
                let param = { postId: item.postId };
                this.commonHttpService.deletePost(param).subscribe(data => {
                    if (data.returnCode) {
                        if (data.returnCode.code == "success") {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                            this.queryClick();
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                        }
                    }

                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }


    // 重置
    reset() {
        this.postsManageBean.postId = "";
        this.postsManageBean.postName = "";
    }
    //分页
    paginate(event) {
        this.rows = event.rows * 1;
        //每页显示的条数
        this.postsManageBean.pageSize = this.rows;
        //当前页
        let currentPage = event.page + 1;
        this.postsManageBean.pageNum = currentPage;

        this.first = event.page * this.postsManageBean.pageSize;
        //调用查询的方法
        this.queryClick();
        // this.postsManageBean.pageNum = 1;
    }


    //返回的值
    outValue(param) {
        this.display = param.display;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }



}