import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from '../../../../../../../app/sharedBasis.module';
import { PostsManageBean } from "../bean/posts-manage.bean";
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
import { checkName, checkRoleNum } from 'app/@uisftech/common/service/validator';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
  selector: 'app-posts-manage-allot-role',
  templateUrl: './posts-manage-allot-role.component.html',
  styleUrls: ['./posts-manage-allot-role.component.scss'],
  providers: [CommonHttpService]
})
export class PostsManageAllotRoleComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //提示信息
  msgs: Message[] = [];
  //分页总数
  total: string;
  //被选中的列表集合
  selectedList: Array<any> = [];
  //所有角色数据列表
  tableData: any;
  //接受父页面传过来的值
  postId: string = '';//角色编号
  postName: string = '';//角色名称
  //分配角色list
  roleRelationList: any;
  postsManageBean: PostsManageBean = new PostsManageBean();
  //右边接受的参数
  selectPower: any = [];
  //接受所有分配权限的值
  power: any = [];
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {

    let param = {
      pageSize: '999999',
      pageNum: '1'
    }
    this.commonHttpService.queryRoleListInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.power = this.commfunc.listToLabelvalue(data.roleListInfo, "roleName", "roleId");
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  ngOnChanges() {
    //接受父页面传过来的值
    this.postId = this.inValue.postId;
    this.postName = this.inValue.postName;
    //查询岗位角色关系
    let param = { postId: this.postId }
    this.commonHttpService.queryPostRoleRelation(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.roleList) {
          this.selectPower = this.commfunc.listToLabelvalue(data.roleList, 'groupName', 'groupId');
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

    //删除左边数据里包含的右边数据
    setTimeout(() => {
      if (this.power) {
        this.selectPower.forEach((item) => {
          this.power.forEach((items, i) => {
            if (items.value == item.value) {
              this.power.splice(i, 1);
            }
          })
        })
      }
    }, 500);
  }

  // 转换成list
  tranList(value) {
    let roleList = [];
    if (value) {
      value.forEach(item => {
        roleList.push(item.value)
      });
    }
    return roleList;
  }
  //确定按钮事件
  preserveClick() {
    let param = { roleList: this.tranList(this.selectPower), postId: this.postId };
    this.commonHttpService.createPostRoleRelation(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit(false);
        this.selectPower = [];
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

  }


  //取消按钮事件
  cancel() {
    this.outValue.emit(false);
  }

}


