import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from '../../../../../../../app/sharedBasis.module';
import { PostsManageBean } from "../bean/posts-manage.bean";
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
import { specialSymbol, checkName, checkRoleNum } from 'app/@uisftech/common/service/validator';
// import { ManagePermission, ManageLevel } from '../../../constant/common.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'
@Component({
  selector: 'posts-add-manage',
  styleUrls: ['posts-add-manage.scss'],
  templateUrl: 'posts-add-manage.html',
  providers: [CommonHttpService, Commfunc]
})

export class PostsAddManage implements OnInit {
  manageLevels: any;
  // manageLevel: string = '';
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //管理权限
  managePermission: any = [];
  //管理级别
  // manageLevel: any = ManageLevel;
  userform: FormGroup; //校验
  inpFlag: boolean = true;//模糊查询
  //提示信息
  msgs: Message[] = [];
  list: any[];
  // orgId: any;
  postsManageBean: PostsManageBean = new PostsManageBean();
  extParam1: any = [];
  kvflag: boolean = true;
  code: any;
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.managePermission = this.code['ManagePermission'];
    this.manageLevels = this.code['manageLevels'];
  }

  ngOnInit() {
    this.extParam1 = [
      { 'key': '', 'value': '' }
    ]
    this.userform = this.fb.group({
      'postName': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5]{1,20}$/), Validators.required]),
      'postId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
      'description': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5a-zA-Z0-9\u3002\uff01\uff0c\u3001\uff1b\uff1a\u201c\u201d\u2018\u2019\uff08\uff09\u300a\u300b\u300b\u3008\u3009\u3010\u3011\u300e\u300f\u300c\u300d\ufe43\ufe44\u3014]{1,250}$/), Validators.required]),
      'managePermission': new FormControl('', Validators.required),
      'manageLevel': new FormControl('', Validators.required),
    })
    this.orgquery('');
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //增加扩展数据
  extParamAdd() {
    this.extParam1.push({ 'key': '', 'value': '' })
  }
  //扩展数据删除
  extParamRemove(item) {
    if (this.extParam1.length > 1) {
      this.extParam1.splice(this.extParam1.indexOf(item), 1)
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '扩展数据不能为空' })
    }
  }
  //扩展数据校验
  preConfirm(data) {
    for (const i in data) {
      if (data[i].key == "" || data[i].value == "") {
        return true
      }
    }
  }
  // 保存
  preserveClick() {
    //扩展数据为空时提示，禁止提交
    if (this.preConfirm(this.extParam1)) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '扩展数据不能为空' })
      return;
    }
    this.postsManageBean.extParam = this.commfunc.transformExtParamStr(this.extParam1);
    if (!(/^[\w\,\|]*$/.test(this.postsManageBean.extParam))) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '扩展数据只能为字母、数字、下划线' })
      return;
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //提交服务
    this.commonHttpService.createPost(this.postsManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //取消
  cancel() {
    this.outValue.emit(false);
    // this.postsManageBean = new PostsManageBean();
  }
  // //机构模糊查询
  inpquery($event) {
    if (this.inpFlag) {
      this.inpFlag = false;
      setTimeout(() => {
        this.inpFlag = true;
        let orgName = document.getElementById('orgName').getElementsByTagName('input')[1].value
        this.orgquery(orgName)
      }, 1000)
    }
  }
  orgquery(orgName) {
    let param = {
      orgId: '',
      orgName: orgName,
      pageSize: '30'
    }
    this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
      if (data.orgList) {
        this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
      }
    })
  }
}